import React, { useEffect, useState, useContext } from "react";
import { makeStyles } from "@material-ui/core/styles";
import MUIDataTable from "mui-datatables";
import MUIDataTableSpanish from "variables/MUIDataTablesSpanish";
import ErrorModal from "components/ErrorModal/ErrorModal";
import {
  customCellStyleSmall,
  customCellStyleMedium,
  customCellStyleLarge,
  customCellStyleStatus,
} from "variables/cellStyles";

const useStyles = makeStyles((theme) => ({
  mainDiv: {
    marginTop: "50px",
    align: "left",
    borderRadius: "1.5em",
    boxShadow: "0px 11px 35px 2px rgba(0, 0, 0, 0.14)",
  },
}));

const ClientContactSummary = ( props ) => {

  const { contacts } = props;

  const classes = useStyles();

  const [forbiddenQuery, setForbiddenQuery] = useState(false);

  const columns = [
    {
      name: "fname",
      label: "Nombre",
      options: {
        filter: true,
        sort: true,
        customBodyRender: customCellStyleSmall,
      },
    },
    {
      name: "lname",
      label: "Apellido",
      options: {
        sortDirection: 'desc',
        filter: true,
        sort: true,
        customBodyRender: customCellStyleSmall,
      },
    },
    {
      name: "email",
      label: "Email",
      options: {
        filter: true,
        sort: true,
        customBodyRender: customCellStyleLarge,
      },
    },
    {
      name: "phone_number",
      label: "Número de contacto",
      options: {
        filter: true,
        sort: true,
        customBodyRender: customCellStyleLarge,
      },
    },
    {
      name: "client_name",
      label: "Cliente asociado",
      options: {
        filter: true,
        sort: true,
        customBodyRender: customCellStyleLarge,
      },
    },
  ];

  const options = {
    ...MUIDataTableSpanish,
    selectableRows: "none",
    filterType: "textField",
    filter: true,
    sort: true,
    rowsPerPage: 5,
    rowsPerPageOptions: [],
    onRowsDelete: false,
  };

  return (
    <div className={classes.mainDiv}>
      <MUIDataTable
        title={"Contactos del cliente"}
        data={contacts}
        columns={columns}
        options={options}
      />
      {/* <ContactModal onAdd={addContact} clients={clientList} /> */}
      {forbiddenQuery && <ErrorModal setOpen={setForbiddenQuery} />}
    </div>
  );
};

export default ClientContactSummary;

