import React, { useState, useEffect, useContext } from "react";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import TextField from "@material-ui/core/TextField";
import Autocomplete from "@material-ui/lab/Autocomplete";
import MenuItem from "@material-ui/core/MenuItem";
import "date-fns";
import DateFnsUtils from "@date-io/date-fns";
import spaLocale from "date-fns/locale/es";
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from "@material-ui/pickers";
//Context
import { UserNameContext, UserPrivilegeContext } from "components/GeneralContextProvider/GeneralContextProvider"
import { isEmpty } from "validator";
//RBAC
import Can from "components/Can/Can"
//General variables
import { statusValues } from "variables/general"

const useStyles = makeStyles((theme) => ({
  mainDiv: {
    marginTop: "50px",
    align: "left",
    borderRadius: "1.5em",
    boxShadow: "0px 11px 35px 2px rgba(0, 0, 0, 0.14)",
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: "25ch",
  },
  button: {
    margin: "10px",
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
  inputReadOnly: {
    color: "grey",
  }
}));

export default function ContactForm(props) {
  const { onEdit, onDelete, clients, editedContact } = props;

  const classes = useStyles();

  //Name of current user to be used in the logs
  const userName = useContext(UserNameContext);
  //Privilege level of the current user
  const userPrivilege = useContext(UserPrivilegeContext);
  //These two are for the Autocomplete component
  const [clientAutocompleteValue, setClientAutocompleteValue] = useState({});
  const [clientAutocompleteInput, setClientAutocompleteInput] = useState("");

  const [contact, setContact] = useState({
    id: 0,
    fname: "",
    lname: "",
    email: "",
    phone_number: "",
    birthday: new Date(),
    client_code: "",
    client_name: "",
  });
  const [edit, setEdit] = useState(false);
  const [contactError, setContactError] = useState({
    fname: false,
    lname: false,
    client_name: false,
  });
  
  //This one is to define whether some buttons are disabled or not based on privileges
  const [isButtonDisabled, setIsButtonDisabled] = useState(true);

  useEffect(() => {
    setContact(editedContact);
    const editedContactClient = clients.filter(
      (client) => client.client_code === editedContact.client_code)[0];
    setClientAutocompleteValue(editedContactClient);
  }, [editedContact]);

  function handleChange(event) {
    const { value, name } = event.target;

    if (name === "fname") {
      setContact((prevState) => {
        return {
        ...prevState,
        fname: value,
        }
      });
    }

    if (name === "lname") {
      setContact((prevState) => {
        return {
        ...prevState,
        lname: value,
        }
      });
    }

    if (name === "email") {
      setContact((prevState) => {
        return {
        ...prevState,
        email: value,
        }
      });
    }

    if (name === "phone_number") {
        setContact((prevState) => {
          return {
          ...prevState,
          phone_number: value,
          }
        });
    }

    if (name === "birthday") {
      setContact((prevState) => {
        return {
        ...prevState,
        note: value,
        }
      });
    }

    if (name === "client_name") {
        setContact((prevState) => {
            return {
            ...prevState,
            client_name: value,
            }
        });
    }

    validateField(!isEmpty(value.toString()), name);
  }

  const validateField = (validator, fieldName) => {
    if (validator) {
      setContactError((prevState) => {
        return { ...prevState, [fieldName]: false };
      });
      return true;
    } else {
      setContactError((prevState) => {
        return { ...prevState, [fieldName]: true };
      });
      return false;
    }
  };

  function validateAll() {
    const check1 = validateField(!isEmpty(contact.fname), "fname");
    const check2 = validateField(!isEmpty(contact.lname), "lname");
    const check3 = validateField(!isEmpty(contact.client_name), "client_name");
    return check1 && check2 && check3;
  }

  function resetFields() {
    setContact({
        id: 0,
        fname: "",
        lname: "",
        email: "",
        phone_number: "",
        birthday: new Date(),
        client_code: "",
        client_name: "",
    });
    setContactError({
      fname: false,
      lname: false,
      client_name: false,
    });
  }

  const handleSave = () => {
    if (!validateAll()) {
      return;
    }
    onEdit(contact);
    setEdit(false);
    resetFields();
  };

  const handleEdit = () => {
    setEdit(true);
  };

  const handleDelete = () => {
    onDelete(contact);
    resetFields();
  };

  const handleCancelEdit = () => {
    setEdit(false);
    resetFields();
    setContact(editedContact);
  };

  return (
    <div className={classes.mainDiv}>
      <Can 
          role={userPrivilege} 
          perform="contacts:edit"
          data={[]} 
          yes={() => {
            setIsButtonDisabled(false);
            return null
          }}
          no={() => {
            setIsButtonDisabled(true);
            return null
          }}
      />
      <DialogTitle id="form-dialog-title">
        Información de Contacto
      </DialogTitle>
      <DialogContent>
        {!edit && (
            <div>
              <TextField
                value={contact.fname}
                name="fname"
                label="Nombre"
                style={{ margin: 8 }}
                placeholder=""
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
                inputProps={
                  { className: classes.inputReadOnly, readOnly: true, }
                }
              />
              <TextField
                value={contact.lname}
                name="lname"
                label="Apellido"
                style={{ margin: 8 }}
                placeholder=""
                helperText={
                    contactError.lname && "Este campo no puede estar vacio"
                  }
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
                inputProps={
                  { className: classes.inputReadOnly, readOnly: true, }
                }
              />
              <TextField
                value={contact.email}
                name="email"
                label="Email"
                style={{ margin: 8 }}
                placeholder=""
                helperText=""
                fullWidth
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
                inputProps={
                  { className: classes.inputReadOnly, readOnly: true, }
                }
              />
              <TextField
                value={contact.phone_number}
                name="phone_number"
                label="Número de contacto"
                style={{ margin: 8 }}
                placeholder=""
                helperText=""
                fullWidth
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
                inputProps={
                  { className: classes.inputReadOnly, readOnly: true, }
                }
              />
              <MuiPickersUtilsProvider utils={DateFnsUtils} locale={spaLocale}>
              <KeyboardDatePicker
                style={{ margin: 16, marginLeft: 8 }}
                label="Fecha de cumpleaños"
                value={contact.birthday}
                views={["month", "day"]}
                readOnly={true}
                onChange={(date) =>
                  setContact((prevState) => {
                    return {
                      ...prevState,
                      birthday: date,
                    };
                  })
                }
                format="dd MMMM"
                inputProps={
                  { className: classes.inputReadOnly, readOnly: true, }
                }
              />
             </MuiPickersUtilsProvider>
              <TextField
                value={contact.client_name}
                name="client_name"
                label="Cliente asociado"
                style={{ margin: 8 }}
                placeholder=""
                helperText=""
                fullWidth
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
                inputProps={
                  { className: classes.inputReadOnly, readOnly: true, }
                }
              />
              <TextField
                value={contact.client_code}
                name="client_code"
                label="RIF"
                style={{ margin: 8 }}
                placeholder=""
                helperText=""
                fullWidth
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
                inputProps={
                  { className: classes.inputReadOnly, readOnly: true, }
                }
              />
            </div>
        )}

        {edit && (
         <div>
         <TextField
           error={contactError.fname}
           onChange={handleChange}
           value={contact.fname}
           name="fname"
           label="Nombre"
           style={{ margin: 8 }}
           placeholder=""
           helperText={
             contactError.fname && "Este campo no puede estar vacio"
           }
           margin="normal"
           InputLabelProps={{
             shrink: true,
           }}
         />
         <TextField
           error={contactError.lname}
           onChange={handleChange}
           value={contact.lname}
           name="lname"
           label="Apellido"
           style={{ margin: 8 }}
           placeholder=""
           helperText={
               contactError.lname && "Este campo no puede estar vacio"
             }
           margin="normal"
           InputLabelProps={{
             shrink: true,
           }}
         />
         <TextField
           onChange={handleChange}
           value={contact.email}
           name="email"
           label="Email"
           style={{ margin: 8 }}
           placeholder=""
           helperText=""
           fullWidth
           margin="normal"
           InputLabelProps={{
             shrink: true,
           }}
         />
         <TextField
           onChange={handleChange}
           value={contact.phone_number}
           name="phone_number"
           label="Número de contacto"
           style={{ margin: 8 }}
           placeholder=""
           helperText=""
           fullWidth
           margin="normal"
           InputLabelProps={{
             shrink: true,
           }}
         />
         <MuiPickersUtilsProvider utils={DateFnsUtils} locale={spaLocale}>
              <KeyboardDatePicker
                style={{ margin: 16, marginLeft: 8 }}
                label="Fecha de cumpleaños"
                value={contact.birthday}
                onChange={(date) =>
                  setContact((prevState) => {
                    return {
                      ...prevState,
                      birthday: date,
                    };
                  })
                }
                format="dd MMMM"
              />
         </MuiPickersUtilsProvider>
         <Autocomplete
              value={clientAutocompleteValue || ""}
              onChange={(event, newClient) => {
                setClientAutocompleteValue(newClient);
                if(newClient) {
                  setContact((prevState) => {
                  return {
                    ...prevState,
                    client_code: newClient.client_code,
                    client_name: newClient.client_name,
                  };
                  })
                }
              }}
              inputValue={clientAutocompleteInput}
              onInputChange={(event, input) => {
                setClientAutocompleteInput(input);
              }}
              options={clients}
              getOptionLabel={(client) => client.client_name}
              name="client_name"
              label="Cliente asociado"
              placeholder=""
              renderInput={(params) => (
                <TextField {...params} label="Cliente asociado" margin="normal" style={{ margin: 8 }} InputLabelProps={{ shrink: true }}/>
              )}
        />
        <TextField
              error={contactError.client_name}
              onChange={handleChange}
              value={contact.client_code}
              name="client_code"
              label="RIF"
              style={{ margin: 8 }}
              placeholder=""
              helperText={
                contactError.client_name && "Este campo no puede estar vacio"
              }
              fullWidth
              margin="normal"
              InputLabelProps={{
                shrink: true,
              }}
              inputProps={
                { className: classes.inputReadOnly, readOnly: true, }
              }
        />
       </div>
        )}
      </DialogContent>
      <DialogActions>
        {!edit && (contact.id > 0 ?
          <>
            <Button
              disabled={isButtonDisabled}
              onClick={handleDelete}
              color="secondary"
              variant="contained"
              className={classes.button}
            >
              Borrar
            </Button>
            <Button
              disabled={isButtonDisabled}
              onClick={handleEdit}
              color="primary"
              variant="contained"
              className={classes.button}
            >
              Editar
            </Button>
          </>
        :
          <>
            <Button
              disabled
              onClick={handleDelete}
              color="secondary"
              variant="contained"
              className={classes.button}
            >
              Borrar
            </Button>
            <Button
              disabled
              onClick={handleEdit}
              color="primary"
              variant="contained"
              className={classes.button}
            >
              Editar
            </Button>
          </>
        )}
        {edit && (
          <>
            <Button
              disabled={isButtonDisabled}
              onClick={handleCancelEdit}
              color="primary"
              variant="contained"
              className={classes.button}
            >
              Cancelar
            </Button>
            <Button
              disabled={isButtonDisabled}
              onClick={handleSave}
              color="primary"
              variant="contained"
              className={classes.button}
            >
              Guardar
            </Button>
          </>
        )}
      </DialogActions>
    </div>
  );
}
