import React, { useState, useContext } from "react";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogTitle from "@material-ui/core/DialogTitle";
import TextField from "@material-ui/core/TextField";
import Autocomplete from "@material-ui/lab/Autocomplete";
import MenuItem from "@material-ui/core/MenuItem";
import { isEmpty } from "validator";
import "date-fns";
import DateFnsUtils from "@date-io/date-fns";
import spaLocale from "date-fns/locale/es";
import {
  MuiPickersUtilsProvider,
  Calendar,
  KeyboardDatePicker,
} from "@material-ui/pickers";
//Context
import { UserPrivilegeContext } from "components/GeneralContextProvider/GeneralContextProvider";
//RBAC
import Can from "components/Can/Can";
//General variables
import { statusValues } from "variables/general";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: "25ch",
  },
  margin: {
    margin: theme.spacing(1),
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
}));

export default function ContactDialog(props) {
  const { onAdd, clients } = props;

  const classes = useStyles();
  //Privilege level of the current user
  const userPrivilege = useContext(UserPrivilegeContext);
  //These two are for the Autocomplete component
  const [clientAutocompleteValue, setClientAutocompleteValue] = useState({});
  const [clientAutocompleteInput, setClientAutocompleteInput] = useState("");

  const [isButtonDisabled, setIsButtonDisabled] = useState(true);
  const [open, setOpen] = useState(false);
  const [contact, setContact] = useState({
    id: 0,
    fname: "",
    lname: "",
    email: "",
    phone_number: "",
    birthday: new Date(),
    client_code: "",
    client_name: "",
  });
  const [contactError, setContactError] = useState({
    fname: false,
    lname: false,
    client_name: false,
  });

  function handleChange(event) {
    const { value, name } = event.target;

    if (name === "fname") {
      setContact((prevState) => {
        return {
          ...prevState,
          fname: value,
        };
      });
    }

    if (name === "lname") {
      setContact((prevState) => {
        return {
          ...prevState,
          lname: value,
        };
      });
    }

    if (name === "email") {
      setContact((prevState) => {
        return {
          ...prevState,
          email: value,
        };
      });
    }

    if (name === "phone_number") {
      setContact((prevState) => {
        return {
          ...prevState,
          phone_number: value,
        };
      });
    }

    if (name === "birthday") {
      setContact((prevState) => {
        return {
          ...prevState,
          note: value,
        };
      });
    }

    if (name === "client_name") {
      setContact((prevState) => {
        return {
          ...prevState,
          client_name: value,
        };
      });
    }

    validateField(!isEmpty(value.toString()), name);
  }

  const validateField = (validator, fieldName) => {
    if (validator) {
      setContactError((prevState) => {
        return { ...prevState, [fieldName]: false };
      });
      return true;
    } else {
      setContactError((prevState) => {
        return { ...prevState, [fieldName]: true };
      });
      return false;
    }
  };

  function validateAll() {
    const check1 = validateField(!isEmpty(contact.fname), "fname");
    const check2 = validateField(!isEmpty(contact.lname), "lname");
    const check3 = validateField(!isEmpty(contact.client_name), "client_name");
    return check1 && check2 && check3;
  }

  function resetFields() {
    setContact({
      id: 0,
      fname: "",
      lname: "",
      email: "",
      phone_number: "",
      birthday: new Date(),
      client_code: "",
      client_name: "",
    });
    setContactError({
      fname: false,
      lname: false,
      client_name: false,
    });
  }

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    resetFields();
    setOpen(false);
  };

  const handleClick = () => {
    if (!validateAll()) {
      return;
    }
    onAdd(contact);
    resetFields();
    setOpen(false);
  };

  return (
    <div>
      <Can
        role={userPrivilege}
        perform="contacts:edit"
        data={[]}
        yes={() => {
          setIsButtonDisabled(false);
          return null;
        }}
        no={() => {
          setIsButtonDisabled(true);
          return null;
        }}
      />
      <Button
        disabled={isButtonDisabled}
        variant="contained"
        size="large"
        color="primary"
        onClick={handleClickOpen}
        className={classes.margin}
      >
        Agregar
      </Button>

      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Agregar Vendedor</DialogTitle>
        <DialogContent>
          <div className={classes.root}>
            <div>
              <TextField
                error={contactError.fname}
                onChange={handleChange}
                value={contact.fname}
                name="fname"
                label="Nombre"
                style={{ margin: 8 }}
                placeholder=""
                helperText={
                  contactError.fname && "Este campo no puede estar vacio"
                }
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
              />
              <TextField
                error={contactError.lname}
                onChange={handleChange}
                value={contact.lname}
                name="lname"
                label="Apellido"
                style={{ margin: 8 }}
                placeholder=""
                helperText={
                  contactError.lname && "Este campo no puede estar vacio"
                }
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
              />
              <TextField
                onChange={handleChange}
                value={contact.email}
                name="email"
                label="Email"
                style={{ margin: 8 }}
                placeholder=""
                helperText=""
                fullWidth
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
              />
              <TextField
                onChange={handleChange}
                value={contact.phone_number}
                name="phone_number"
                label="Número de contacto"
                style={{ margin: 8 }}
                placeholder=""
                helperText=""
                fullWidth
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
              />
              <MuiPickersUtilsProvider utils={DateFnsUtils} locale={spaLocale}>
                <KeyboardDatePicker
                  style={{ margin: 16, marginLeft: 8 }}
                  label="Fecha de cumpleaños"
                  value={contact.birthday}
                  onChange={(date) =>
                    setContact((prevState) => {
                      return {
                        ...prevState,
                        birthday: date,
                      };
                    })
                  }
                  format="dd MMMM"
                />
              </MuiPickersUtilsProvider>
              <Autocomplete
                value={clientAutocompleteValue || ""}
                onChange={(event, newClient) => {
                  setClientAutocompleteValue(newClient);
                  if (newClient) {
                    setContact((prevState) => {
                      return {
                        ...prevState,
                        client_code: newClient.client_code,
                        client_name: newClient.client_name,
                      };
                    });
                  }
                }}
                inputValue={clientAutocompleteInput}
                onInputChange={(event, input) => {
                  setClientAutocompleteInput(input);
                }}
                options={clients}
                getOptionLabel={(client) => client.client_name || ""}
                name="client_name"
                label="Cliente asociado"
                renderInput={(params) => (
                  <TextField
                    {...params}
                    label="Cliente"
                    margin="normal"
                    style={{ margin: 8 }}
                    InputLabelProps={{ shrink: true }}
                  />
                )}
              />
              <TextField
                disabled
                error={contactError.client_name}
                onChange={handleChange}
                value={contact.client_code}
                name="client_code"
                label="RIF"
                style={{ margin: 8 }}
                placeholder=""
                helperText={
                    contactError.client_name && "Este campo no puede estar vacio"
                }
                fullWidth
                margin="normal"
                InputLabelProps={{
                  shrink: true,
                }}
              />
            </div>
          </div>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancelar
          </Button>
          <Button onClick={handleClick} color="primary">
            Agregar
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

