import React, { useEffect, useState, useContext } from "react";
import MUIDataTable from "mui-datatables";
import MUIDataTableSpanish from "variables/MUIDataTablesSpanish";
import ContactModal from "components/ContactModal/ContactModal";
import ContactForm from "components/ContactForm/ContactForm";
import ErrorModal from "components/ErrorModal/ErrorModal";
import {
  customCellStyleSmall,
  customCellStyleMedium,
  customCellStyleLarge,
  customCellStyleStatus,
} from "variables/cellStyles";
//Context
import { ContactTableContext, UpdateContactTableContext, UpdateErrorStatusContext, UserContext } from "components/GeneralContextProvider/GeneralContextProvider"
import {
  getAllContacts,
  createContact,
  updateContact,
  removeContact,
} from "repositories/ContactRepository";
import {
    getAllClients,
  } from "repositories/ClientRepository";

const ContactTable = () => {
  const [editedContact, setEditedContact] = useState({});
  const [edit, setEdit] = useState(false);
  const [forbiddenQuery, setForbiddenQuery] = useState(false);
  const [clientList, setClientList] = useState([]);

  const tableState = useContext(ContactTableContext);
  const setTableState = useContext(UpdateContactTableContext);
  const setErrorStatus = useContext(UpdateErrorStatusContext);
  const userToken = useContext(UserContext);

  useEffect(() => {
    setTableState((prevValue) => {
      return {
        ...prevValue,
        loading: true,
      };
    });
    getAllContacts(setTableData, onQueryError, userToken);
    getAllClients(setClientList, onQueryError, userToken);
  }, []);

  function addContact(contact) {
    createContact(
      contact,
      onQuerySuccess,
      onQueryError,
      userToken
    );
  }

  function editContact(contact) {
    updateContact(
      contact,
      onQuerySuccess,
      onQueryError,
      userToken
    );
  }

  function deleteContact(contact) {
    let contactId = contact.id;
      removeContact(
        contactId,
        onQuerySuccess,
        onQueryError,
        userToken
      );
  }

  function handleRowClick(rowData, rowMeta) {
    const contact = tableState.data[rowMeta.dataIndex];
    setEditedContact(contact);
    setEdit(true);
  }

  function onQuerySuccess(statusCode) {
    setTableState((prevValue) => {
      return {
        ...prevValue,
        loading: true,
      };
    });
    getAllContacts(setTableData, onQueryError, userToken);
  }

  function onQueryError(error) {
    let statusCode = parseInt(error.message);
    if (statusCode===403) {
      setForbiddenQuery(true);
    } else if (statusCode===401) {
      //Here goes some modal about unauthorized operation
    } else {
      setErrorStatus(true);
    }
  }

  function setTableData(fetchedData) {
    setTableState({ data: fetchedData, loading: false });
  }

  function closeEditModal() {
    setEdit(false);
  }

  const columns = [
    {
      name: "fname",
      label: "Nombre",
      options: {
        filter: true,
        sort: true,
        customBodyRender: customCellStyleSmall,
      },
    },
    {
      name: "lname",
      label: "Apellido",
      options: {
        sortDirection: 'desc',
        filter: true,
        sort: true,
        customBodyRender: customCellStyleSmall,
      },
    },
    {
      name: "email",
      label: "Email",
      options: {
        filter: true,
        sort: true,
        customBodyRender: customCellStyleLarge,
      },
    },
    {
      name: "phone_number",
      label: "Número de contacto",
      options: {
        filter: true,
        sort: true,
        customBodyRender: customCellStyleLarge,
      },
    },
    {
      name: "client_name",
      label: "Cliente asociado",
      options: {
        filter: true,
        sort: true,
        customBodyRender: customCellStyleLarge,
      },
    },
  ];

  const options = {
    ...MUIDataTableSpanish,
    selectableRows: "none",
    filterType: "textField",
    filter: true,
    sort: true,
    rowsPerPage: 5,
    rowsPerPageOptions: [],
    onRowClick: handleRowClick,
    onRowsDelete: false,
  };

  return (
    <div>
      <MUIDataTable
        title={"Lista de Contactos"}
        data={tableState.data}
        columns={columns}
        options={options}
      />
      <ContactModal onAdd={addContact} clients={clientList} />
      <ContactForm onEdit={editContact} onDelete={deleteContact} clients={clientList} editedContact={editedContact} />
      {forbiddenQuery && <ErrorModal setOpen={setForbiddenQuery} />}
    </div>
  );
};

export default ContactTable;

//https://www.smashingmagazine.com/2020/06/rest-api-react-fetch-axios/
//https://itnext.io/centralizing-api-error-handling-in-react-apps-810b2be1d39d
